<template>
    <v-row class="fill-height my-5" justify="center">
        <v-card cols="12" variant="text" class="pa-1" max-width="1000">
            <v-card-title class="d-flex align-start pe-2 text-h6" style="width: 1000px;">
                <v-icon>mdi-home</v-icon>
                HOME
            </v-card-title>
            <v-card class="pa-4" outlined>
                <div class="subtitle-2 mb-2">방문 통계</div>
                <v-row>
                    <v-col cols="6" class="d-flex align-center">
                        <v-icon class="mr-2">mdi-calendar-today</v-icon>
                        <div>
                            <div class="caption">Today</div>
                            <div class="headline">{{ today ?? 0 }}</div>
                        </div>
                    </v-col>
                    <v-col cols="6" class="d-flex align-center">
                        <v-icon class="mr-2">mdi-account-group</v-icon>
                        <div>
                            <div class="caption">Total</div>
                            <div class="headline">{{ total ?? 0 }}</div>
                        </div>
                    </v-col>
                </v-row>
            </v-card>
            <v-card class="pa-4 mt-3" outlined>
                <v-icon>mdi-account</v-icon>
                <v-card-text>
                    안녕하십니까! 저는 5년 차 개발자 김혁준이라고 합니다.<br/>
                    제 개인 홈페이지(자기소개 및 포트폴리오)에 방문 주셔서 너무나 감사합니다.<br/>
                    원래는 PPT를 사용하여 포트폴리오를 작성하려 했지만, 그보다 웹 페이지로 녹여내는 것이
                    좀 더 개발자 다운 생각이라 하여 이렇게 웹 페이지를 개발하게 되었습니다.<br/>
                    비록 GitHub에 배포하여 간단하고 심플하게 제작되었어도 '프로젝트 소개' 텝에서 제가 지금까지 
                    개발 및 유지 보수하였던 프로젝트들을 편하게 구경했으면 합니다. <br/>
                    소개 글은 여기까지 하고 행복한 하루 보내시기 바랍니다 :)
                </v-card-text>
                <v-card-text class="d-flex justify-end">
                    <v-tooltip text="제 개인 인스타입니다. O_<">
                        <template #activator="{ props }">
                            <v-btn
                                v-bind="props"
                                @click="openInsta"
                                target="_blank"
                                rel="noopener"
                                icon
                                variant="text"
                                aria-label="Instagram"
                                size="36"
                            >
                                <v-img width="20" height="20" cover :src="insta" alt="Instagram"/>
                            </v-btn>
                        </template>
                    </v-tooltip>
                </v-card-text>
            </v-card>
        </v-card>
    </v-row>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import insta from '@/assets/instagram.png'
export default {
    name: 'DashboardView',
    data() {
        return {
            instagramUrl: 'https://www.instagram.com/98_k.jun/',
            insta: insta,
        }
    },
    computed: {
        ...mapGetters('countStore', ['today', 'total']),
    },
    methods: {
        ...mapActions('countStore', ['hit']),
        openInsta() {
            window.open(this.instagramUrl, '_blank', 'noopener')
        }
    },
    mounted() {
        this.$store.commit('countStore/ROLLOVER')
        this.hit({ oncePerSession: true })
    },
    components: {

    }

}
</script>

<style>
    .chart-wrap { width: 100%; }
</style>